/* MEDIA QUERIES BREAKPOINTS */
.marketTable th:first-child,
.marketTable td:first-child {
  flex-basis: 148px;
  text-align: center; }
  @media (max-width: 480px) {
    .marketTable th:first-child,
    .marketTable td:first-child {
      flex-basis: 100px; } }
  @media (max-width: 359px) {
    .marketTable th:first-child,
    .marketTable td:first-child {
      flex-basis: 28% !important; } }

.marketTable th:nth-child(2),
.marketTable td:nth-child(2) {
  flex-basis: 70px;
  text-align: right; }
  @media (max-width: 480px) {
    .marketTable th:nth-child(2),
    .marketTable td:nth-child(2) {
      flex-basis: 60px; } }
  @media (max-width: 359px) {
    .marketTable th:nth-child(2),
    .marketTable td:nth-child(2) {
      flex-basis: 23% !important; } }

.marketTable th:nth-child(3),
.marketTable td:nth-child(3) {
  flex-basis: 123px;
  text-align: right; }
  @media (max-width: 480px) {
    .marketTable th:nth-child(3),
    .marketTable td:nth-child(3) {
      flex-basis: 100px; } }
  @media (max-width: 359px) {
    .marketTable th:nth-child(3),
    .marketTable td:nth-child(3) {
      line-height: 16px;
      margin: 3px 0;
      flex-basis: 29% !important; } }

.pair {
  font-weight: 600;
  display: flex; }
  .pair span {
    width: 50%; }
  .pair span:first-child {
    text-align: right;
    margin-right: 5px; }
  .pair span:last-child {
    text-align: left;
    margin-left: 5px;
    color: rgba(74, 74, 74, 0.6); }

.createDaiBtn {
  float: right;
  margin-top: -1px; }
  @media (max-width: 359px) {
    .createDaiBtn {
      position: relative;
      bottom: 6px;
      font-size: 12px; } }



