/* MEDIA QUERIES BREAKPOINTS */
.table {
  max-width: 100%; }
  .table th:first-child,
  .table td:first-child {
    flex-basis: 78px;
    text-align: right; }
    @media (max-width: 480px) {
      .table th:first-child,
      .table td:first-child {
        flex-basis: 50px; } }
    @media (max-width: 359px) {
      .table th:first-child,
      .table td:first-child {
        flex-basis: 15%; } }
  .table th:nth-child(2),
  .table td:nth-child(2) {
    flex-basis: 128px;
    text-align: right; }
    @media (max-width: 480px) {
      .table th:nth-child(2),
      .table td:nth-child(2) {
        flex-basis: 95px; } }
    @media (max-width: 359px) {
      .table th:nth-child(2),
      .table td:nth-child(2) {
        flex-basis: 20%; } }
  .table th:nth-child(3),
  .table td:nth-child(3) {
    flex-basis: 128px;
    text-align: right; }
    @media (max-width: 480px) {
      .table th:nth-child(3),
      .table td:nth-child(3) {
        flex-basis: 100px; } }



