/* MEDIA QUERIES BREAKPOINTS */
.container {
  max-width: 980px; }

.row {
  justify-content: flex-end; }

.table td, .table th {
  text-align: right; }

.table td {
  font-weight: 500; }

.table th:first-child,
.table td:first-child {
  flex-basis: 148px;
  text-align: right; }
  @media (max-width: 480px) {
    .table th:first-child,
    .table td:first-child {
      flex-basis: 80px; } }
  @media (max-width: 359px) {
    .table th:first-child,
    .table td:first-child {
      flex-basis: 70px; } }

.table td:first-child {
  color: #EF5350; }

.table th:nth-child(2),
.table td:nth-child(2) {
  flex-basis: 88px;
  text-align: right; }
  @media (max-width: 480px) {
    .table th:nth-child(2),
    .table td:nth-child(2) {
      flex-basis: 75px; } }

.table th:nth-child(3),
.table td:nth-child(3) {
  flex-basis: 88px;
  text-align: right; }
  @media (max-width: 480px) {
    .table th:nth-child(3),
    .table td:nth-child(3) {
      flex-basis: 75px; } }

.table th:last-child,
.table td:last-child {
  width: 0;
  flex-basis: 0;
  padding: 0;
  margin: 0;
  flex-grow: 0; }



