/* MEDIA QUERIES BREAKPOINTS */
@media (max-width: 480px) {
  .base {
    flex-wrap: wrap; } }

.base hr {
  display: none; }

@media (max-width: 359px) {
  .base span {
    font-size: 8px !important; } }

.red {
  color: #EF5350; }

.green {
  color: #26A69A; }

.bolderText {
  font-weight: 500; }

.buying {
  margin-bottom: 0.75em; }

.detailsAmountCol {
  flex-basis: 45%; }
  @media (max-width: 480px) {
    .detailsAmountCol {
      flex-basis: 100%; } }

.detailsTradingCol {
  flex-basis: 65%; }
  @media (max-width: 480px) {
    .detailsTradingCol {
      flex-basis: 100%; } }

.detailsTradingFirstCol {
  flex-basis: 55%; }
  @media (max-width: 480px) {
    .detailsTradingFirstCol {
      flex-basis: 100%; } }

.detailsTradingSecCol {
  flex-basis: 45%; }
  @media (max-width: 480px) {
    .detailsTradingSecCol {
      flex-basis: 100%; } }

.tradingStatus {
  border: 1px solid #19B092;
  color: #26A69A;
  height: 20px;
  border-radius: 6px;
  display: inline-block;
  width: 92px;
  text-align: center; }
  @media (max-width: 359px) {
    .tradingStatus {
      height: 17px; } }



