/* MEDIA QUERIES BREAKPOINTS */
.container {
  max-width: 980px; }

.row {
  justify-content: flex-end; }

.headerMainSection {
  margin-bottom: 30px;
  align-items: center; }
  @media (max-width: 480px) {
    .headerMainSection {
      margin-bottom: 0; } }

@media (max-width: 768px) {
  .leftColumn {
    margin-bottom: 15px; }
    .leftColumn * {
      font-size: 9px !important; } }

@media (max-width: 359px) {
  .leftColumn * {
    font-size: 7px !important; } }

@media (min-width: 768px) and (max-width: 1024px) {
  .leftColumn {
    margin-bottom: 20px; } }

@media (max-width: 768px) {
  .leftColumn .infoSection {
    position: relative;
    top: 5px; } }

@media (max-width: 359px) {
  .leftColumn .infoSection {
    position: relative;
    top: 4px; } }

@media (min-width: 768px) and (max-width: 992px) {
  .leftColumn * {
    font-size: 12px !important; } }



