/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.container {
  max-width: 980px; }

.row {
  justify-content: flex-end; }

.recipientAddress {
  max-width: 100px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis; }
  @media (max-width: 480px) {
    .recipientAddress {
      max-width: 40px;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis; } }

.frame {
  align-self: flex-start; }

.table td,
.table th {
  text-align: right; }

.table td {
  font-weight: 500; }

.table th:first-child,
.table td:first-child {
  flex-basis: 70px;
  text-align: right; }
  @media (max-width: 480px) {
    .table th:first-child,
    .table td:first-child {
      flex-basis: 60px; } }
  @media (max-width: 359px) {
    .table th:first-child,
    .table td:first-child {
      flex-basis: 14% !important; } }

.table td:first-child {
  font-weight: 500;
  color: #b1b1b3;
  font-size: 12px;
  line-height: 20.4px; }

.table th:nth-child(2),
.table td:nth-child(2) {
  flex-basis: 40px;
  padding-left: 0;
  padding-right: 0;
  text-align: center;
  text-transform: uppercase; }
  @media (max-width: 480px) {
    .table th:nth-child(2),
    .table td:nth-child(2) {
      flex-basis: 35px; } }
  @media (max-width: 359px) {
    .table th:nth-child(2),
    .table td:nth-child(2) {
      flex-basis: 12% !important; } }

@media (max-width: 359px) {
  .table td:first-child {
    line-height: 14px;
    margin: 2px 0; } }

.table th:nth-child(3),
.table td:nth-child(3) {
  flex-basis: 100px;
  flex-grow: 0;
  text-align: left;
  height: 19px; }
  @media (max-width: 480px) {
    .table th:nth-child(3),
    .table td:nth-child(3) {
      flex-basis: 40px; } }
  @media (max-width: 359px) {
    .table th:nth-child(3),
    .table td:nth-child(3) {
      flex-basis: 14%; } }

.table th:nth-child(4),
.table td:nth-child(4) {
  flex-basis: 46px;
  text-align: center; }
  @media (max-width: 480px) {
    .table th:nth-child(4),
    .table td:nth-child(4) {
      flex-basis: 35px; } }
  @media (max-width: 359px) {
    .table th:nth-child(4),
    .table td:nth-child(4) {
      flex-basis: 12% !important; } }

.table th:nth-child(5),
.table td:nth-child(5) {
  flex-basis: 58px;
  text-align: right; }
  @media (max-width: 359px) {
    .table th:nth-child(5),
    .table td:nth-child(5) {
      flex-basis: 15% !important; } }



