/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

table.table {
  max-width: 100%; }
  table.table td, table.table th {
    text-align: right;
    margin-left: 8px;
    margin-right: 8px; }
    @media (max-width: 768px) {
      table.table td, table.table th {
        margin-left: 6px;
        margin-right: 6px; } }
    @media (max-width: 359px) {
      table.table td, table.table th {
        margin: 0;
        flex-basis: 14% !important; } }
  table.table td {
    font-weight: 500; }
  table.table th:first-child,
  table.table td:first-child {
    flex-basis: 72px;
    text-align: center; }
    @media (max-width: 768px) {
      table.table th:first-child,
      table.table td:first-child {
        flex-basis: 65px; } }
