@import url("https://netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-glyphicons.css");
/* MEDIA QUERIES BREAKPOINTS */
/* COLOURS */
/* CUSTOM SCROLLBAR TABLE SCROLLING */
.t-scroll tbody::-webkit-scrollbar {
  width: 8px; }

.t-scroll tbody::-webkit-scrollbar-track {
  background-color: #fafafa;
  padding: 20px; }

.t-scroll tbody::-webkit-scrollbar-thumb {
  background-color: #eeeeee;
  border-radius: 5.5px; }

/* MIXIN TABLE SCROLLING */
/* / MIXIN TABLE SCROLLING */
.full-width {
  width: 100%; }

.inlineBlock {
  display: inline-block; }

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px; }

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5); }

body {
  background-color: #f8f7f5;
  margin-top: 40px;
  font-family: "Roboto", sans-serif; }
  @media (max-width: 480px) {
    body {
      margin-top: 12px; } }

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  margin-top: 0;
  margin-bottom: 20px;
  color: #222228;
  letter-spacing: 3.99px; }

table {
  width: 100%; }

.bold {
  font-weight: 700; }

.no-padding {
  padding: 0; }

@media (min-width: 1024px) {
  hr {
    display: none !important; } }

@media (min-width: 768px) and (max-width: 992px) {
  :global body {
    margin-left: 15%;
    margin-right: 15%; } }

@media (max-width: 480px) {
  :global .ReactModal__Body--open {
    overflow-y: hidden !important;
    max-height: 100vh; } }



