/* MEDIA QUERIES BREAKPOINTS */
.OasisLogoWrapper {
  display: flex;
  align-items: center;
  width: 103px;
  height: 40px; }

.OasisLogo {
  cursor: pointer;
  width: 100%;
  height: 37px;
  background: url("./../assets/logo-oasis.png") no-repeat;
  background-size: 100%; }
  .OasisLogo:hover {
    background-image: url("./../assets/logo-oasis-hover.png"); }
  @media (max-width: 480px) {
    .OasisLogo {
      background-size: 91%; } }
  @media (max-width: 359px) {
    .OasisLogo {
      background-size: 80%; } }



