.NoEthereumSection {
  max-width: 980px;
  margin: 0 auto;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  color: #222228;
  font-size: 24px; }
  @media (max-width: 480px) {
    .NoEthereumSection {
      max-width: 100%; } }
  .NoEthereumSection h2 {
    max-width: 100%;
    font-weight: 700;
    opacity: 0.8;
    color: #222228; }
  .NoEthereumSection .ImgHeaderLogo {
    width: 264px;
    padding-bottom: 30px; }
  .NoEthereumSection .ImgEthereumLogo {
    width: 61px;
    padding-top: 30px;
    margin-bottom: 40px; }
  .NoEthereumSection .HorizontalLine {
    color: #DCDDE0;
    border: 0;
    border-top: solid 1px #DCDDE0; }



