/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.OasisWidgetFrame {
  position: relative;
  display: inline-block;
  background: white;
  border: 1px solid #eff1f3;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.13);
  border-radius: 4px;
  width: 442px;
  max-width: 100%;
  min-height: 305px;
  margin: 0 16px 36px 16px;
  padding: 20px 0 0;
  z-index: 0; }
  @media (max-width: 768px) {
    .OasisWidgetFrame {
      width: 100%; } }
  @media (max-width: 480px) {
    .OasisWidgetFrame {
      margin: 12px 0; } }
  @media (max-width: 359px) {
    .OasisWidgetFrame {
      min-height: 290px; } }
  .OasisWidgetFrame .Heading {
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
    font-weight: 400;
    text-align: left;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 18px; }
    @media (max-width: 768px) {
      .OasisWidgetFrame .Heading {
        height: 30px;
        margin-bottom: 0;
        font-size: 16px; }
        .OasisWidgetFrame .Heading > span {
          position: relative;
          bottom: 4px; } }
    @media (max-width: 359px) {
      .OasisWidgetFrame .Heading {
        height: auto; }
        .OasisWidgetFrame .Heading > span {
          position: static; }
        .OasisWidgetFrame .Heading :global .selectContainer {
          position: relative !important;
          bottom: 3px; } }

.OasisWidgetContent {
  padding: 0 30px; }
  @media (min-width: 480px) and (max-width: 1024px) {
    .OasisWidgetContent {
      margin-bottom: 20px; } }

.WidgetFloatRightButton {
  float: right;
  position: relative;
  top: -1px; }

.OasisWidgetFooter {
  position: absolute;
  bottom: 30px;
  left: 30px;
  right: 30px; }

@media (max-width: 480px) {
  .NoContentPaddingXXS {
    padding: 0; } }

.HeadingText {
  top: 5px !important; }



