/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.infoText {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: 500;
  margin-left: 5px; }
  @media (max-width: 480px) {
    .infoText {
      font-family: "Montserrat", sans-serif;
      font-size: 10px;
      font-weight: 500;
      margin-left: 15px; } }



