/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.LockedAccountSection {
  text-align: center;
  color: #222228; }
  .LockedAccountSection * {
    max-width: 100% !important; }
  @media (max-width: 768px) {
    .LockedAccountSection {
      max-width: 100% !important; } }
  .LockedAccountSection .Logo {
    width: 264px;
    padding-bottom: 30px; }
  .LockedAccountSection .Separator {
    color: #DCDDE0;
    border: 0;
    border-top: solid 1px #DCDDE0; }
  .LockedAccountSection .Heading {
    text-transform: uppercase;
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
    font-weight: 700;
    opacity: 0.8;
    color: #222228;
    letter-spacing: 4px; }
  .LockedAccountSection .Message {
    font-family: "Montserrat", sans-serif;
    font-size: 25px;
    font-weight: 500;
    opacity: 0.50;
    width: 925px;
    display: inline-block;
    margin-top: 50px;
    margin-bottom: 70px;
    letter-spacing: 0.6px; }
    @media (max-width: 1024px) {
      .LockedAccountSection .Message {
        font-family: "Montserrat", sans-serif;
        font-size: 16px;
        font-weight: 500; } }



