/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

thead {
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
  font-weight: 700;
  line-height: 2;
  text-transform: uppercase;
  opacity: 0.68;
  color: #222228;
  background-color: #F8F7F5; }

@media (max-width: 480px) {
  th, td {
    font-size: 10px !important;
    margin-left: 4px;
    margin-right: 4px;
    padding: 5px 6px; } }

@media (max-width: 359px) {
  th, td {
    font-size: 9px; } }

tbody {
  font-family: 'Roboto', sans-serif; }

tr.clickable {
  cursor: pointer;
  border-top: 1px transparent solid;
  border-bottom: 1px transparent solid; }

tr.clickable:hover {
  border-bottom: 1px #DCDDE0 solid;
  border-top: 1px #DCDDE0 solid;
  background: rgba(248, 247, 245, 0.15); }

tr.active {
  background: rgba(248, 247, 245, 0.5);
  border-top: 1px #E8E9EA solid;
  border-bottom: 1px #E8E9EA solid;
  cursor: pointer; }

td, th {
  padding: 5px 11px;
  margin: 0;
  flex-basis: 0;
  flex-grow: 0;
  flex-shrink: 0;
  overflow: hidden;
  box-sizing: content-box; }

table.scrolling tr {
  display: flex; }

table.scrolling th,
table.scrolling td {
  max-width: 100%;
  display: block;
  padding: 0;
  margin: 5px 11px; }
  @media (max-width: 480px) {
    table.scrolling th,
    table.scrolling td {
      font-size: 10px !important;
      margin-left: 8px !important;
      margin-right: 8px !important;
      line-height: 26px;
      vertical-align: middle;
      margin-bottom: 0;
      margin-top: 2px; } }
  @media (max-width: 359px) {
    table.scrolling th,
    table.scrolling td {
      font-size: 9px; } }

table.scrolling tbody {
  overflow-x: hidden;
  overflow-y: auto;
  display: block;
  margin-top: 10px;
  height: 160px;
  width: 98%; }
  @media (max-width: 480px) {
    table.scrolling tbody {
      width: 100%; } }

tr.collapseRow {
  border-bottom: 1px #DCDDE0 solid;
  border-top: 1px #DCDDE0 solid;
  margin-bottom: 1px; }
  tr.collapseRow > .collapseCell {
    font-size: 12px;
    color: #666669;
    text-transform: uppercase; }
    tr.collapseRow > .collapseCell:first-child:last-child {
      flex-grow: 1;
      flex-basis: 100%;
      text-align: center;
      font-weight: 500; }

.scrolling tr.emptyFallback {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .scrolling tr.emptyFallback > td:first-child:last-child {
    flex-grow: 1;
    flex-basis: 100%;
    text-align: center;
    font-weight: 500; }

.isInitializing {
  cursor: not-allowed !important; }

@media (max-width: 480px) {
  .dateHeading {
    flex-basis: 60px !important; } }

@media (max-width: 480px) {
  .dateCell {
    flex-basis: 60px !important;
    position: relative !important;
    top: 3px !important; } }

@media (max-width: 480px) {
  .dateHeadingTwoRows {
    flex-basis: 35px !important; } }

@media (max-width: 480px) {
  .twoRows {
    position: relative !important;
    bottom: 4px !important;
    text-align: center !important;
    line-height: 14px !important;
    flex-basis: 35px !important; } }



