/* MEDIA QUERIES BREAKPOINTS */
.box {
  display: flex;
  max-width: 100%; }

.wrap {
  flex-wrap: wrap; }

@media (max-width: 480px) {
  .wrapXXS {
    flex-wrap: wrap; } }

@media (max-width: 480px) {
  .inlineXXS {
    display: inline-flex; } }

.vertical {
  flex-direction: column; }

.horizontal {
  flex-direction: row; }



