/* MEDIA QUERIES BREAKPOINTS */
.LoadProgress {
  border: 1px solid #b1b1b3;
  color: #68686C;
  font-size: 11px;
  padding: 5px 0;
  float: right;
  font-weight: bold;
  border-radius: 7%;
  width: 130px;
  text-align: center;
  letter-spacing: normal;
  position: relative;
  top: 1px; }
  @media (max-width: 768px) {
    .LoadProgress {
      width: 100px; } }
  @media (max-width: 359px) {
    .LoadProgress {
      width: 60px;
      padding: 2px 0; } }



