/* MEDIA QUERIES BREAKPOINTS */
.base {
  align-items: baseline; }

.detailsTradingFirstCol {
  flex-basis: 55%;
  flex-grow: 1;
  text-align: right;
  padding-right: 20px; }
  @media (max-width: 480px) {
    .detailsTradingFirstCol {
      text-align: left;
      font-size: 10px; } }

.bolderText {
  font-weight: 500; }

.estimateUSD {
  background-color: #f9f8f8;
  border-radius: 6px;
  border: 1px solid #efefef;
  color: #939194;
  display: inline-block;
  font-size: 12px;
  height: 22px;
  line-height: 1.5;
  padding: 0;
  text-align: center;
  vertical-align: middle;
  width: 92px; }
  @media (max-width: 480px) {
    .estimateUSD {
      font-size: 10px;
      height: 18px;
      line-height: 18px;
      vertical-align: middle; } }



