/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.link {
  max-width: 150px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 600; }
  @media (max-width: 480px) {
    .link {
      max-width: 70px;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis; } }



