/* MEDIA QUERIES BREAKPOINTS */
.textDanger {
  color: #EF5350; }

.spaceLeft, .spaceBoth {
  margin-left: 0.35em; }

.spaceRight, .spaceBoth {
  margin-right: 0.35em; }

.OasisAccount {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  font-weight: 500;
  text-align: right;
  margin-bottom: 10px; }
  @media (max-width: 480px) {
    .OasisAccount > .label {
      position: absolute;
      top: 9px;
      left: 30px; } }

.AccountSelection {
  display: inline-block;
  width: 330px;
  border: 1px solid #D3D5DC;
  border-radius: 100px;
  margin-left: 25px;
  padding: 0 16px 0 2px;
  font-size: 10px; }
  @media (min-width: 768px) and (max-width: 1024px) {
    .AccountSelection {
      width: 80%; } }
  @media (max-width: 480px) {
    .AccountSelection {
      margin-left: 0;
      width: 100%; } }
  .AccountSelection select {
    width: 100%;
    -webkit-appearance: none;
    background-color: transparent;
    color: rgba(34, 34, 40, 0.68);
    border: none;
    margin-right: -18px;
    padding: 13px 33px 13px 17px;
    z-index: 0;
    position: relative;
    cursor: pointer;
    font-family: "Montserrat", sans-serif;
    font-size: 10px;
    font-weight: 500; }
    @media (max-width: 480px) {
      .AccountSelection select {
        padding-left: 60px; } }
    @media (max-width: 480px) {
      .AccountSelection select {
        letter-spacing: 0.25px;
        padding-right: 10px;
        font-family: "Montserrat", sans-serif;
        font-size: 9px;
        font-weight: 500; } }
    @media (min-width: 768px) and (max-width: 1024px) {
      .AccountSelection select {
        font-family: "Montserrat", sans-serif;
        font-size: 13px;
        font-weight: 400;
        letter-spacing: 0.6px; } }
    .AccountSelection select option {
      font-family: "Montserrat", sans-serif;
      font-size: 10px;
      font-weight: 400; }
  .AccountSelection .glyphicon {
    color: #D3D5DC; }



